*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
  font-family: 'Open Sans', sans-serif;
}

body {
  font-family: inherit;
}

:root {
  --color-white: #fff;
  --color-light-white: #eee;

  --color-tomato: #182225;
  --color-pink-transparent: rgba(8, 8, 19, 0.9);
  --color-purple-transparent: rgb(50, 68, 80);

  --color-orange: #ff3c00;

  --color-light-black: #222;
  --color-black: #000;

  --shadow: rgba(0,0,0, .4)

}
